<template>
  <div>
    <h2>用户添加信息</h2>
    <form action="">
      用户名:<input v-model="user.name" type="text"><br>
      年龄:<input v-model="user.age" type="text"><br>
      生日:<input v-model="user.bir" type="text"><br>
      <input type="button" @click="saveUserInfo" value="添加用户信息">
    </form>
  </div>
</template>

<script>
  export default{
    name:"UserAdd",
    data(){
      return{
        user:{}
      }
    },
    methods:{
      saveUserInfo(){
        console.log(this.user);
        this.$http.post("http://localhost:8089/vue/user/add",this.user).then(res=>{
          console.log(res.data)
          console.log(this)
          if(res.data.success){
            this.$router.push("/user")
          }
        })
      }
    }
  }
</script>

<style>
</style>



